
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  overflow-x: hidden;
}
html{
  font-size: 62.3%;
  text-transform: capitalize;
}
.navbar{
  background-color: black;
  padding: 1.2rem 0;
  
}
.navbar-brand{
  font-size: 2.3rem;
  color: white!important;
}
.nav-link{
  color: white!important;
  font-size: 1.9rem;
  margin-left: 1rem;
}
.btn-zee{
  font-size: 1.8rem; 
  margin-left: 1rem;
  border-radius: 2rem;
}
.navbar-toggler{
background-color: white!important;
}
.landing{
   height: 89vh;
   color: white;
   display: flex;
   justify-content: center;
  align-items: center;
  flex-direction: column;
 text-align: center;  
 color: wheat;

}
.Heading{
  font-size: 9rem;
  line-height: 7rem;
  font-family: 'MonteCarlo', cursive;
  font-weight: 900;
  letter-spacing: 1.2rem;
  color: #eecf1d;
}
.sub_Heading{
  font-size: 2.8rem;
  line-height: 1.2rem;
  color: #eecf1d;
}
.description{
  font-size: 1.5rem;
}
.images{
  width: 40rem;
  height: 50rem;
}


.ourstory{
  width: 50rem;
  height: 40rem;
}
.card-clip{
  clip-path: circle(25.3% at 100% 0);
  transition:all 0.5s ;
}
.card-clip:hover{
  clip-path: circle(141.4% at 100% 0);
}
.footer{
  font-size: 1.5rem;
}



/* media query ///////////////////////////////////////////////*/
@media screen and (max-width: 980px) {
  html{
    font-size: 55%;
  }
  body{
    overflow-x: hidden;
  }
  .navbar-brand{
    margin: auto;
  }
  .navbar-toggler{
    margin-right:auto ;
  }
}
@media screen and (max-width: 780px) {
  html{
    font-size: 45%;
  }
  

}
@media screen and (max-width: 480px) {
  html{
    font-size: 35%;
  }

}
@media screen and (max-width: 300px) {
  html{
    font-size: 25%;
  }
 
}